.volunteer_section a,
.partner_section a,
.charity_shop_section a,
.sms_help_section a {
  display: inline-block;
  margin-left: 83px;
	margin-top: 20px;
	text-decoration: none;
	margin-top: 20px;
  text-align:center;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	line-height: 16px;
