  }
}

.sms_help_section a {
margin-left: 93px;

&:hover,
&:focus {
  margin-left: 99px;
  transition: .4s;
  opacity: 0.7;  
  }
}

.charity_shop_section a {
 margin-left: 103px; 

&:hover,
&:focus {
  margin-left: 109px;
  transition: .4s;
  opacity: 0.7;  
  }
}

.volunteer_section a::after,
.partner_section a::after,
.charity_shop_section a::after,
.sms_help_section a::after {
	content: '';
	position: absolute;
	top: 0px;
	left: calc(100% + 10px);
	width: 15px;
	height: 15px;
	background: url("../img/svg/arrow_right_black.svg") no-repeat center;
}

.partner_section a::after {
	left: calc(100% + 10px);
}

.charity_shop_section a::after,
.sms_help_section a::after {
	left: calc(100% + 10px);
}

.volunteer_section .volunteer_section_tip_second,
.partner_section .partner_section_tip_second,
.charity_shop_section .charity_shop_section_tip_second,
.sms_help_section_tip_second {
 display: none;  
}

.volunteer_section a,
.partner_section a,
.charity_shop_section a,
.sms_help_section a {
 display: none;  
}

