  left: 40px;
}

.sms_pop_up_details span:nth-of-type(2) {
  top: 195px;
  left: 40px;
}

.sms_pop_up_hide_second {
  float: right;
  margin-right: 26px;
  margin-top: -655px;
  position: relative;
  width: 30px;
  height: 30px;
  background: transparent;
  cursor: pointer;
}

.sms_pop_up_hide_second::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  width: 30px;
  height: 3px;
  transform: rotate(45deg);
}

.sms_pop_up_hide_second::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  width: 30px;
  height: 3px;
  transform: rotate(135deg);
}

.about_us_sms_pop_up .sms_pop_up_details {
  margin: 15% auto 0px auto !important;
}

