@import "partials/mixins";

$desk-regular-proxima: "ProximaNova-Regular", sans-serif;
$desk-bold-proxima: "ProximaNova-Bold", sans-serif;
$desk-regular-raleway: "Raleway-Regular", sans-serif;
$desk-bold-raleway: "Raleway-Bold", sans-serif;
$desk-light-proxima: "ProximaNova-Light", sans-serif;

$mob-font-regular: "SFNS-Regular", sans-serif;
$mob-font-bold: "SFNS-Bold", sans-serif;
$mob-font-thin: "SFNS-Thin", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
$arrowcolor: #E78B48;

.header_top_sms_pop_up {
	display: none; /* Hidden by default */
	position: fixed; /* Stay in place */
	z-index: 5001; /* Sit on top */
	padding-top: 300px; /* Location of the box */
	left: 0;
	top: 0;
	width: 100%; /* Full width */
	height: 100%; /* Full height */
	overflow: auto; /* Enable scroll if needed */
	background-color: rgb(0, 0, 0); /* Fallback color */
	background-color: rgba(0, 0, 0, 0.9); /* Black w/ opacity */
}

.header_top_sms_pop_up .header_top_sms_pop_up_message {
	margin: auto;
	display: block;
	width: 450px;
	height: 450px;
	max-width: 700px;
	background: #fff;

	p {
		display: inline-block;
		font-family: $desk-bold-proxima;
		color: #333;
		margin-left: 20px;
		margin-top: 0px;
		white-space: pre-line;
		width: 400px;
		height: 100px;
		font-style: italic;
		font-size: 16px;
		line-height: 20px;
	}

	p:nth-of-type(1) {
		margin-top: 40px;
		margin-bottom: 0;
	}
}

.close_top_sms_pop_up {
	position: absolute !important;
	top: 75px !important;
	left: 90% !important;
	color: #fff !important;
	font-size: 40px !important;
	font-weight: bold !important;
	transition: 0.3s !important;
	width: 50px !important;
}

.close_top_sms_pop_up:hover,
.close_top_sms_pop_up:focus {
	color: #bbb;
	text-decoration: none;
	cursor: pointer;
}

