	div {
		position: relative;
		background: $Bghover-color;
		width: 318px;
		min-height: 222px;
		cursor: pointer;
	}
}

.clickable_section:hover,
.clickable_section:focus {
	background: #fff !important;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
}

