@import "partials/page_header";
@import "partials/page_footer";
@import "partials/header_top_sms_pop_up";
@import "partials/mixins";
@import "partials/about_us_partials/zaporuka_projects_help_form";
@import "partials/about_us_partials/about_us_help_section";
@import "partials/news_partials/all_news_mobile";
@import "partials/news_partials/all_news_desktop";

.home_header_bottom_nav li a sup {
	top: -6.5px !important;
}

.all_news {
	margin-top: 60px;
	height: 1400px;
	min-height: 1000px;
	position: relative;
	border-top: 2px solid $arrowcolor;
	background: inherit;
	width: 100%;
}

.all_news_news,
.all_news_projects,
.all_news_activity {
	display: inline-block;
	position: absolute;
	top: 105px;
	white-space: nowrap;
	height: 19px;
	width: 84px;
	padding-bottom: 38px;
	opacity: 0.3;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 16px;
	line-height: 19px;
	border: none;
	cursor: pointer;

	&:hover,
	&:focus {
		color: $arrowcolor;
		border-bottom: 2px solid $arrowcolor;
		opacity: 1;
	}
}

.active-click {
	color: $arrowcolor;
	border-bottom: 2px solid $arrowcolor;
	opacity: 1;
}

.all_news_line {
	top: 180px;
	left: 113px;
	position: absolute;
	background-color: #D8D8D8;
	height: 1px;
	width: 1140px;
}

.all_news_news {
	left: 114px;
	width: 84px;
}

.all_news_projects {
	left: 300px;
	width: 68px;
}

.all_news_activity {
	left: 470px;
	width: 82px;
}

.projects_news_block_group,
.all_projects_block_group,
.all_activity_block_group {
	display: flex;
	flex-wrap: wrap;
	height: 1140px;
	width: 1140px;
	margin-left: 114px;
	margin-top: 242px;
	justify-content: space-between !important;
}

.projects_news_block_group_item1 {
	background: url("../img/content/other_projects_1.png") no-repeat center !important;
	white-space: nowrap;
	position: relative;
	width: 353px !important;
	height: 340px;
	box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;
}

.projects_news_block_group_item1 h2 {
	height: 24px;
	width: 215px;
	margin-left: 40px;
	margin-top: 229px;
	color: #fff;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	line-height: 24px;
	z-index: 50;
}

.projects_news_block_group_item1 i {
	font-style: normal;
	color: #FFF !important;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	position: absolute;
	bottom: 40px;
	left: 40px;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.projects_news_block_group_item1 i::after {
	content: '';
	position: absolute;
	top: 0px;
	left: 130px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right_white.svg") no-repeat center !important;
}

.projects_news_block_group_item2,
.projects_news_block_group_item3,
.projects_news_block_group_item4,
.projects_news_block_group_item5,
.projects_news_block_group_item6 {
	position: relative;
	height: 340px;
	width: 353px !important;
	box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;
	background: #fff;

	span:nth-of-type(1) {
		display: inline-block;
		margin-left: 40px;
		margin-right: 101px;
		margin-top: 30px;
		height: 16px;
		width: 71px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}

	span:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 66px;
		left: 40px;
		height: 1px;
		width: 273px;
		opacity: 0.25;
		background-color: #A5A5A5;
	}

	span:nth-of-type(2) {
		display: inline-block;
		margin-top: 30px;
		height: 17px;
		width: 100px;
		color: #A5A5A5;
		font-family: "ProximaNova-Light", sans-serif;
		font-size: 14px;
		line-height: 17px;
	}
}

.projects_news_block_group_item2 h2,
.projects_news_block_group_item4 h2,
.projects_news_block_group_item5 h2 {
	height: 24px;
	width: 215px;
	margin-left: 42px;
	margin-top: 51px;
	margin-bottom: 0;
	color: #333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	white-space: nowrap;
	line-height: 24px;
}

.projects_news_block_group_item2 b,
.projects_news_block_group_item5 b {
	display: inline-block;
	margin-left: 42px;
	margin-top: 21px;
	height: 104px;
	max-width: 273px;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.projects_news_block_group_item4 b {
	display: inline-block;
	margin-left: 42px;
	margin-top: 21px;
	height: 104px;
	max-width: 273px;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.projects_news_block_group_item3 h2,
.projects_news_block_group_item6 h2 {
	height: 74px;
	width: 203px;
	margin-left: 42px;
	margin-top: 51px;
	margin-bottom: 0;
	color: #333;
	font-family: 'ProximaNova-Bold', sans-serif;
	font-size: 20px;
	line-height: 24px;

	br {
		display: none;
	}
}

.projects_news_block_group_item3 b,
.projects_news_block_group_item6 b {
	display: inline-block;
	margin-top: 21px;
	margin-left: 42px;
	height: 104px;
	max-width: 273px;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.projects_news_block_group div i,
.all_projects_block_group div i,
.all_activity_block_group div i {
	font-style: normal;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	white-space: nowrap;
	position: absolute;
	bottom: 40px;
	left: 40px;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.projects_news_block_group_item2:focus,
.projects_news_block_group_item3:focus,
.projects_news_block_group_item4:focus,
.projects_news_block_group_item5:focus,
.projects_news_block_group_item6:focus,
.projects_news_block_group_item2:hover,
.projects_news_block_group_item3:hover,
.projects_news_block_group_item4:hover,
.projects_news_block_group_item5:hover,
.projects_news_block_group_item6:hover {
	h2,
	span,
	b,
	i {
		color: #fff !important;
	}

	i::after {
		content: '';
		background: url("../img/svg/arrow_right_white.svg") no-repeat center !important;
	}

	span:nth-of-type(1)::after {
		content: '';
		background: #fff;
	}

	background: linear-gradient(224.05deg, #F3AE43 0%, #E78B48 100%) !important;
}

.projects_news_block_group div:not(:first-of-type) i::after,
.all_projects_block_group div:not(:first-of-type) i::after,
.all_activity_block_group div:not(:first-of-type) i::after {
	content: '';
	position: absolute;
	top: 0px;
	left: 130px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.additional_item1,
.additional_item2,
.additional_item3,
.additional_item4,
.additional_item5,
.additional_item6 {
	display: none;
}

.other_news_more,
.other_projects_more,
.other_activity_more {
	margin-top: 5px;
	margin-left: 582px;
	width: 300px;

	&:hover,
	&:focus {
		opacity: 0.7;
		transition: .4s;
		margin-left: 588px;
	}

	ul {
		display: inline-block;
		width: 50px;
		list-style-type: none;

		li {
			display: inline-block;
			height: 3px;
			width: 3px;
			margin-right: 2px;
			background-color: #E78B48;
		}
	}

	button.to_more_news,
	button.to_more_projects,
	button.to_more_activity {
		display: inline-block;
		margin-left: -33px;
		min-width: 112px;
		background: none;
		outline: none;
		border: none;
		color: #E78B48;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 14px;
		font-weight: bold;
		word-spacing: 2px;
		line-height: 16px;
		cursor: pointer;
	}
}

.all_projects_block_group,
.all_activity_block_group {
	display: none;
}

.other_projects_more,
.other_activity_more {
	display: none;
}

.all_news_mobile {
	display: none;
}

.display_flex {
	display: flex !important;
}

//FOOTER EXCEPTIONS

.page_footer {
	margin-top: 120px;
}

// Footer MEDIA QUERY 320-1024

@media screen and (min-width: 320px) and (max-width: 1024px) {
	.page_footer {
		margin-top: 60px !important;
	}
}
