@import "../mixins";

//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.all_news_mobile {
		margin-top: -20px;
		height: 1298px;
		position: relative;
		border-top: 2px solid $arrowcolor;
		background: inherit;
    z-index:100;
		width: 100%;
	}

	.all_news_news_mobile,
	.all_news_projects_mobile,
	.all_news_activity_mobile {
		display: inline-block;
		position: absolute;
		top: 60px;
		left: 35px;
		white-space: nowrap;
		height: 16px;
		width: 76px;
		opacity: 0.3;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 17px;
		border: none;
		cursor: pointer;

		&:hover,
		&:focus {
			color: $arrowcolor;
			opacity: 1;
			border: unset;
		}
	}

	.active-click {
		color: $arrowcolor;
		opacity: 1;
	}

	.active-click::before {
		content: '';
		position: absolute;
		left: -25px;
		top: 7px;
		height: 3px;
		width: 20px;
		background-color: #e78b48;
	}

	.all_news_news_mobile {
		left: 15px;
		top: 60px;
		width: 84px;
	}

	.all_news_projects_mobile {
		left: 15px;
		top: 95px;
		width: 68px;
	}

	.all_news_activity_mobile {
		left: 15px;
		top: 130px;
		width: 82px;
	}

	.projects_news_block_group_mobile,
	.all_projects_block_group_mobile,
	.all_activity_block_group_mobile {
		display: flex;
		height: 1038px;
		width: 290px;
		margin-left: 15px;
		margin-top: 242px;
		flex-direction: column;

		div {
			width: 290px !important;
			height: 314px;
			margin-bottom: 20px;
		}
	}

	.projects_news_block_group_item1 {
		background: url("../img/content/other_projects_1.png") no-repeat center !important;
		white-space: nowrap;
		position: relative;
		width: 290px;
		height: 314px;
		box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
		cursor: pointer;
	}

	.projects_news_block_group_item1 h2 {
		height: 24px;
		width: 215px;
		margin-left: 40px;
		margin-top: 229px;
		color: #fff;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		line-height: 24px;
		z-index: 50;
	}

	.projects_news_block_group_item1 i {
		font-style: normal;
		color: #FFF;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		font-weight: bold;
		line-height: 16px;
		position: absolute;
		bottom: 40px;
		left: 40px;

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.projects_news_block_group_item1 i::after {
		content: '';
		position: absolute;
		top: 0px;
		left: 130px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right_white.svg") no-repeat center;
	}

	.projects_news_block_group_item2,
	.projects_news_block_group_item3,
	.projects_news_block_group_item4,
	.projects_news_block_group_item5,
	.projects_news_block_group_item6 {
		position: relative;
		height: 314px;
		width: 290px;
		box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
		cursor: pointer;
		background: #fff;

		span:nth-of-type(1) {
			display: inline-block;
			margin-left: 20px;
			margin-right: 95px;
			margin-top: 21px;
			height: 14px;
			width: 61px;
			color: #A5A5A5;
			font-family: $desk-regular-raleway;
			font-size: 12px;
			line-height: 15px;
			letter-spacing: 1px;
			text-transform: uppercase;
		}

		span:nth-of-type(1)::after {
			content: '';
			position: absolute;
			top: 55px;
			left: 18px;
			height: 1px;
			width: 245px;
			opacity: 0.25;
			background-color: #A5A5A5;
		}

		span:nth-of-type(2) {
			display: inline-block;
			margin-top: 20px;
			height: 17px;
			width: 100px;
			color: #A5A5A5;
			font-family: $desk-light-proxima;
			font-size: 12px;
			line-height: 15px;
		}
	}

	.projects_news_block_group_item2 h2,
	.projects_news_block_group_item4 h2,
	.projects_news_block_group_item5 h2 {
		height: 24px;
		width: 215px;
		margin-left: 20px;
		margin-top: 41px;
		margin-bottom: 0;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 18px;
		white-space: nowrap;
		line-height: 26px;
	}

	.projects_news_block_group_item2 b,
	.projects_news_block_group_item5 b {
		display: inline-block;
		margin-left: 20px;
		margin-top: 21px;
		height: 104px;
		max-width: 273px;
		color: #333;
		font-weight: normal;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 24px;
	}

	.projects_news_block_group_item4 b {
		display: inline-block;
		margin-left: 20px;
		margin-top: 21px;
		height: 104px;
		max-width: 273px;
		color: #333;
		font-weight: normal;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 24px;
	}

	.projects_news_block_group_item3 h2,
	.projects_news_block_group_item6 h2 {
		height: 74px;
		width: 203px;
		margin-left: 20px;
		margin-top: 41px;
		margin-bottom: 0;
		color: #333;
		font-family: $desk-bold-proxima;
		font-size: 18px;
		line-height: 26px;

		br {
			display: none;
		}
	}

	.projects_news_block_group_item3 b,
	.projects_news_block_group_item6 b {
		display: inline-block;
		margin-top: 21px;
		margin-left: 20px;
		height: 104px;
		max-width: 273px;
		color: #333;
		font-weight: normal;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 26px;
	}

	.projects_news_block_group_mobile div i,
	.all_projects_block_group_mobile div i,
	.all_activity_block_group_mobile div i {
		font-style: normal;
		color: #E78B48;
