	.projects_news_block_group_mobile div i::after,
	.all_projects_block_group_mobile div i::after,
	.all_activity_block_group_mobile div i::after {
		content: '';
		position: absolute;
		top: 0px;
		left: 130px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

