	.projects_news_block_group_mobile .additional_mob_item2,
	.projects_news_block_group_mobile .additional_mob_item3,
	.projects_news_block_group_mobile .additional_mob_item4,
	.projects_news_block_group_mobile .additional_mob_item5,
	.projects_news_block_group_mobile .additional_mob_item6,
	.projects_news_block_group_mobile .additional_mob_item7,
	.projects_news_block_group_mobile .additional_mob_item8,
	.projects_news_block_group_mobile .additional_mob_item9 {
		display: none;
	}

	.all_projects_block_group_mobile .additional_mob_item1,
	.all_projects_block_group_mobile .additional_mob_item2,
	.all_projects_block_group_mobile .additional_mob_item3,
	.all_projects_block_group_mobile .additional_mob_item4,
