@font-face {
	font-family: "SFNS-Regular";
	src: url("../fonts/SFNS/San_Francisco_Display_Regular.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Bold";
	src: url("../fonts/SFNS/San_Francisco_Display_Bold.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Thin";
	src: url("../fonts/SFNS/San_Francisco_Display_Thin.otf") format("opentype");
}

@font-face {
	font-family: "ProximaNova-Regular";
	src: url("../fonts/ProximaNova/ProximaNova-Regular.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Regular";
	src: url("../fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Bold";
	src: url("../fonts/ProximaNova/ProximaNova-Bold.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Bold";
	src: url("../fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Light";
	src: url("../fonts/ProximaNova/ProximaNova-Light.ttf") format("truetype");
}

body {
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: #FBFBFB;
	color: #333;
	font-family: 'ProximaNova-Regular','ProximaNova-Bold','Raleway-Regular','Raleway-Bold', sans-serif;
	font-size: 14px;
	line-height: auto;
}

.container {
	width: 1367px;
	margin: 0 auto;
	max-width: 100%;
}


@media screen and (max-width: 1024px) {
	.header_height_increase {
		margin-bottom: 420px;
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
	}
}

@media screen and (min-width: 361px) and (max-width: 375px) {
	.header_height_increase {
		margin-bottom: 752px !important;
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
	}
}

@media screen and (min-width: 1025px) {
.sticky {
  position: fixed !important;
  top: 0;
  z-index: 5000;
  background:#FBFBFB;
  height: 80px;
  border-bottom: 2px solid #E78B48;

  .home_header_top {
  display:none !important;  
  }
 }  
}

$desk-regular-proxima: "ProximaNova-Regular", sans-serif;
$desk-bold-proxima: "ProximaNova-Bold", sans-serif;
$desk-regular-raleway: "Raleway-Regular", sans-serif;
$desk-bold-raleway: "Raleway-Bold", sans-serif;
$desk-light-proxima: "ProximaNova-Light", sans-serif;

$mob-font-regular: "SFNS-Regular", sans-serif;
$mob-font-bold: "SFNS-Bold", sans-serif;
$mob-font-thin: "SFNS-Thin", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
$arrowcolor: #E78B48;

.home_block {
	background: #FBFBFB;
	position: relative;
}

.home_header {
	width: 1367px;
	max-height: 124px;

	.home_header_top {
		position: relative;
		height: 45px;
		width: 100%;
		background: #F3F3F3;
		color: #F2F2F2;

		img:nth-of-type(1) {
			position: absolute;
			bottom: 18px;
			left: 114px;
		}

		img:nth-of-type(2) {
			position: absolute;
			bottom: 19px;
			left: 284px;
		}
	}
}

.home_header_top_tel,
.home_header_mobile_tel {
	display: inline-block;
	height: 15px;
	margin-left: 134px;
	margin-top: 15px;
	margin-right: 0;
	width: 120px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
  text-decoration: none;
}

.home_header_top_email,
.home_header_mobile_email {
	display: inline-block;
	height: 15px;
	margin-left: 51px;
	margin-top: 15px;
	width: 120px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
  text-decoration: none;
}

.home_header_top_help,
.home_header_mobile_help {
	float: right;
	margin-right: 107px;
	margin-top: 15px;
	height: 15px;
	width: 262px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
  cursor: pointer;
}

.home_header_top_help:hover,
.home_header_top_help:focus {
color: #333;  
}

.home_header_bottom {
	position: relative;
	min-width: 75px;
	display: block;
	background: inherit;

	.home_header_bottom_logo,
  .home_header_bottom_logo_mobile {
		position: absolute;
		top: 15px;
		left: 101px;
		height: 46.89px;
		width: 178px;

		img {
			width: 100%;
			height: 100%;
		}
	}

  
  .home_header_bottom_logo_mobile {
   left: 17px !important; 
   top: 10px !important;
  }

  .home_header_bottom_logo_mobile {
  display: none; 

  img {
			width: 131px !important;
			height: 31px !important;
		}
  }

	.home_header_bottom_nav {
		position: absolute;
		list-style-type: none;
		text-align: middle;
		left: 332px;
		top: 17px;
		min-width: 651px;
		color: #333;
		font-family: "Raleway-Regular", sans-serif;
		font-weight: 500;
		font-size: 14px;
		line-height: 16px;

		li {
			display: inline-block;
			margin-right: 16px;
			padding-right: 15px;

			a {
				text-decoration: none;
				color: #7C7C7C;

				sup {
					position: absolute;
					top: 1px;
					right: 4px;
          font-size: 10px;
					color: #E78B48;
					font-family: "ProximaNova-Bold", sans-serif;
					font-weight: bold;
          display:none;
				}
			}
		}
	}
}

.home_header_bottom_nav li a sup::before {
content:"+";
top:0px;
left:-6.5px;
position:absolute;
height:2px;
width:2px;
font-family: "ProximaNova-Bold", sans-serif;
font-weight: bold;
color: #E78B48;
}

.home_header_bottom_nav li a:hover,
.home_header_bottom_nav li a:focus {
	color: $arrowcolor;
}

.home_header_bottom_nav_trigger {
	display: none;
	background: unset;
}

.home_header_bottom_nav li:nth-of-type(3) {
	margin-right: 19px;
}

.home_header_bottom_nav li:nth-of-type(4) {
	position: relative;
	margin-right: 16px;
}

.home_header_bottom_nav li:nth-of-type(5) {
	margin-right: 17px;
}

.home_header_bottom_nav li:nth-of-type(6) {
	margin-right: 19px;
}

.home_header_bottom_nav li:nth-of-type(7) {
	margin-right: 0px;
}

.home_header_bottom_languagepicker {
	position: absolute;
	top: 16px;
	left: 1034px;
	display: inline-block;
	list-style-type: none;
	color: #979797;
	font-family: 'Raleway-Regular', sans-serif;
	font-weight: 500;
	font-size: 14px;
	line-height: 16px;
	width: 41px;
	height: 16px;
	overflow: hidden;
	transition: all .3s ease;
	vertical-align: middle;

	&:hover,
	&focus {
		height: 100px;
	}

	a {
		text-decoration: none;
		color: #979797 !important;

		li {
			width: 25px;
			margin-bottom: 10px;
			position: relative;
			font-weight: normal;
		}

		li:hover,
		li:focus {
			color: #E78B48;
		}

		li:last-of-type {
			font-weight: none;
      overflow:visible;
		}

		li.ukr::after {
			content: '';
			position: absolute;
			top: 6px;
			left: 32px;
			width: 8px;
			height: 4px;
			background: url("../img/ui/arrow_down.png") no-repeat center;
		}
	}
}

.home_header_bottom_help {
	float: right;
	position: relative;
	margin-right: 136px;
	margin-top: 31px;
	color: #E78B48;
	font-family: "Raleway-Bold", sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	cursor: pointer;
  overflow: visible;

  &:hover,
  &:focus {
  margin-right: 131px;
  transition: .4s;
  opacity: 0.7;  
  }

	a {
		text-decoration: none;
		color: inherit;
	}
}

.home_header_bottom_help::after {
	content: '';
	position: absolute;
	top: 2px;
	left: calc(100% + 10px);
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.home_header_nav_menu_mobile {
	position: relative;
	display: none !important;
	position: relative;
}


.home_header_nav_menu_mobile .conf a,
.home_header_nav_menu_mobile .conf_ref a {
text-decoration:none;
color:inherit;  
} 

.home_header_nav_menu_mobile .home_header_bottom_help {
	display: none !important;
}

.HeaderChangeBG {
	background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
}

.darkenImg {
filter: brightness(-50%);  
}

.help_block_trigger {
cursor: pointer;
}

.burger_transform {
	margin-top: 26px !important;

	.bar1 {
		transform: rotate(-45deg) translate(-4px, -1px);
		transition: ease-in 0.3s;
		background: #fff;
	}

	.bar2 {
		opacity: 0;
		background: #fff;
	}

	.bar3 {
		transform: rotate(45deg) translate(-10px, -7px);
		transition: ease-in 0.3s;
		background: #fff;
	}
}

