.page_footer {
	position: relative;
	display: inline-block;
	margin-top: 143px;
	width: 100%;
	border-top: 2px solid #E78B48;

	img:first-of-type {
		margin-top: 44px;
		margin-left: 97.5px;
		max-width: 100%;
	}

	a {
		text-decoration: none;
		color: inherit;
	}
}

.page_footer_social {
	list-style-type: none;
	width: 120px;
	margin-left: 75px;
	margin-top: 24px;

	li {
		width: 100%;
		cursor: pointer;
		margin-bottom: 22px;

		i {
			color: #BFBFBF;
			font-size: 15px;
			margin-right: 17px;
		}

		span {
			height: 19px;
			width: 74px;
			color: #333333;
			font-family: 'Raleway-Regular', sans-serif;
			font-size: 16px;
			line-height: 19px;
		}
	}
}

.page_footer_social li:hover,
.page_footer_social li:focus {
	i,
	span {
		color: #E78B48 !important;
	}
}

.page_footer_social li:nth-of-type(1) i {
	margin-right: 22px;
}

.page_footer_about_us,
.page_footer_activity,
.page_footer_help,
.page_footer_contacts {
	position: absolute;
	top: 42px;
	left: 346px;
	display: inline-block;
	list-style-type: none;
	width: 75px;
	color: #333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 19px;

	li {
		width: 100%;
		margin-bottom: 23px;
	}

	li:nth-of-type(1) {
		font-family: 'Raleway-Bold', sans-serif;
		margin-bottom: 39px;
		margin-top: 3px;
	}
}

.page_footer_activity {
	top: 43px;
	left: 540px;
}

.page_footer_help {
	left: 743px;
	width: 161px;

	li:nth-of-type(1) {
		font-family: 'Raleway-Bold', sans-serif;
		margin-bottom: 39px;
		margin-top: 5px;
	}
}

.page_footer_contacts {
	top: 44px;
	left: 1020px;
	min-width: 195px;

	li {
		margin-bottom: 20px;
		position: relative;
	}

	li:nth-of-type(1) {
		margin-top: 4px;
		margin-bottom: 35px;
	}

	li:nth-of-type(2) {
		font-family: 'ProximaNova-Light', sans-serif;
		color: black;
		line-height: 28px;
		margin-bottom: 43px;
	}
}

.page_footer_contacts li:nth-of-type(3) {
	margin-left: 27px;
	margin-bottom: 11px;
	font-family: 'ProximaNova-Light', sans-serif;
	color: black;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.page_footer_contacts li:nth-of-type(3)::before {
	content: '';
	position: absolute;
	top: 3px;
	left: -27px;
	width: 10px;
	height: 10px;
	background: url("../img/ui/phone_icon.png") no-repeat;
}

.page_footer_contacts li:nth-of-type(4) {
	margin-left: 27px;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.page_footer_contacts li:nth-of-type(4)::before {
	content: '';
	position: absolute;
	top: 7px;
	left: -27px;
	width: 10px;
	height: 10px;
	background: url("../img/ui/email_icon.png") no-repeat;
}

small,
.conf,
.conf_ref {
	display: inline-block;
	margin-left: 111px;
	margin-top: 75px;
	opacity: 0.6;
	color: #333333;
	font-family: $desk-regular-proxima;
	font-size: 12px;
	line-height: 14px;

	strong {
		font-family: $desk-regular-proxima;
		opacity: 0.6;
	}

	a {
		text-decoration: none;
		color: inherit;
	}
}

.conf {
	margin-left: 100px;
}

.conf_ref {
	margin-left: 35px;
}

.developed,
.designed {
  position: relative;
  display:inline-block;
	color: #333;
  margin-left: 114px;
	font-family: $desk-light-proxima;
	font-size: 12px;
	line-height: 14px;
	white-space: nowrap;

	a {
		margin-left: 8px;
		text-decoration: none;
		color: #E78B48;
		font-family: $desk-bold-raleway;
		font-size: 12px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 1.4px;
		line-height: 15px;
	}
}

.developed::before,
.designed::before {
	content: '';
	position: absolute;
	width: 6px;
	height: 6px;
	top: 3px;
	left: -13px;
	background: #AAAAAA;
}

.designed {
	margin-left: 128px;

	a {
		text-transform: none !important;
		margin-left: 8px;
    letter-spacing: 0px;
	}
}

